* {
  padding: 0;
  margin: 0;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.Auth-Container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  background-color: darkslategray;
  box-sizing: border-box;
  color: white;
}

.Login-Box {
  width: 400px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 10px;
  border-radius: 5px;
  gap: 10px;
}

.Login-Box h1,
.Login-Box span {
  width: 100%;
}


.Login-Box input {
  padding: 10px;
  width: 80%;
  margin: 10px 0;
  border-radius: 5px;
  transition: 0.5s;
  border: none;
}

.Login-Box button {
  padding: 10px;
  background: white;
  outline: none;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  color: darkslategray;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  transition: 0.5s;
}

.Login-Box button:hover {
  box-shadow: 0px 0px 5px black;
}

.Login-Box button svg {
  transform: translateX(-3px);
  transition: 0.4s;
}

.Login-Box button:hover svg {
  transform: translateX(0px);
}


.Login-Box input:focus {
  outline: none;
  box-shadow: 0px 0px 5px black;

}

.Login-Box button:focus {
  outline: none;
}

.Chat-Container {
  height: 100vh;
  background: darkslategray;
}